.main {
  padding-bottom: 45px;
  &__slider {
    position: relative;
  }
  &-slide {
    display: flex;
    align-items: center;
    gap: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 24px;
    padding: 34px 82px 34px 34px;
  }
  &-slide__desc {
    position: relative;
    z-index: 2;
    max-width: 550px;
  }
  &-slide__img {
    position: relative;
    width: 500px;
    flex-shrink: 0;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
    }
  }
  &__title {
    font-size: 46px;
    line-height: 110%;
    font-weight: 600;
    letter-spacing: -0.92px;
    color: #fff;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 110%;
    font-weight: 500;
    letter-spacing: -0.6px;
    color: #E6B964;
    margin-top: 4px;
  }
  &__btn {
    height: 68px;
    display: inline-flex;
    gap: 14px;
    padding: 5px 34px;
    margin-top: 60px;
    span {
      font-size: 18px;
    }
  }
  &__slider-nav {
    position: absolute;
    z-index: 2;
    right: 34px;
    bottom: 34px;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    width: 37px;
    height: 37px;
    margin: 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 45px / 4));
    gap: 15px;
    margin-top: 15px;
  }
  &__item {
    position: relative;
    &:hover {
      .main__item-img {
        img {
          transform: scale(1.125);
        }
      }
    }
    @media screen and (min-width: 1151px) {
      &--big {
        grid-column: 1/3;
        .main__item-img {
          padding-bottom: 37.1%;
        }
      }
    }
    &-img {
      position: relative;
      display: block;
      border-radius: 24px;
      padding-bottom: 76%;
      overflow: hidden;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 24px;
        transition: all 0.5s;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    &-btn {
      position: absolute;
      left: 28px;
      bottom: 28px;
      height: 56px;
      gap: 14px;
      padding: 5px 24px;
      span {
        font-size: 18px;
      }
    }
  }
}
.info {
  padding: 45px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    gap: 10px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #F1F7F7;
    border-radius: 24px;
    padding: 34px 17px 34px 17px;
    &-icon {
      width: 48px;
      height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #006B6B;
      border-radius: 12px;
    }
    &-text {
      font-size: 18px;
      line-height: 155%;
      font-weight: 500;
      margin-top: 14px;
    }
  }
}
.products2 {
  padding: 45px 0;
  &__cards {
    display: grid;
    grid-template-columns: 1.25fr 0.916fr 0.916fr 0.916fr;
    gap: 15px;
    margin-top: 25px;
  }
  &__btn {
    height: 64px;
    padding: 5px 15px;
    margin-top: 25px;
    span {
      font-size: 18px;
    }
  }
}
.product2-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F1F7F7;
  border-radius: 14px;
  padding: 16px 8px;
  transition: all 0.3s;
  @media screen and (min-width: 993px) {
    &:nth-child(1) {
      grid-row: 1/3;
    }
    &:nth-child(2) {
      grid-column: 2/4;
    }
    &:nth-child(5) {
      grid-column: 3/5;
    }
  }
  &:hover {
    background: #F2DAAF;
    .product2-card__btn {
      background: #fff;
      .btn-brown__arrow {
        background: #F2DAAF;
      }
    }
  }
  &__img {
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__btn {
    position: absolute;
    left: 24px;
    bottom: 24px;
    max-width: calc(100% - 48px);
    height: 56px;
    gap: 14px;
    padding: 5px 24px;
    span {
      font-size: 18px;
    }
  }
}
.products {
  padding: 45px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 45px / 4));
    gap: 15px;
    margin-top: 25px;
  }
  &__btn {
    height: 64px;
    padding: 5px 15px;
    margin-top: 25px;
    span {
      font-size: 18px;
    }
  }
}
.product-card {
  background: #F1F7F7;
  box-shadow: 0px 4px 12.8px 0px rgba(0, 0, 0, 0.06);
  border-radius: 16px;
  padding: 14px;
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 74%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__favorite {
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 7px;
    svg {
      width: 18px;
      height: auto;
    }
  }
  .favorite--active {
    svg path {
      fill: #006B6B;
    }
  }
  &__desc {
    margin-top: 10px;
  }
  &__name {
    font-size: 18px;
    line-height: 155%;
    font-weight: 500;
  }
  &__size {
    font-size: 14px;
    line-height: 170%;
    color: #8E8E8E;
    margin-top: 10px;
  }
  &__price {
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    color: #006B6B;
    margin-top: 10px;
  }
}
.banner {
  padding: 45px 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
    background-image: url("../img/banner-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 24px;
    padding: 50px 60px;
  }
  &__left {
    position: relative;
    z-index: 2;
    max-width: 500px;
  }
  &__right {
    position: relative;
    z-index: 2;
    .banner__subtitle {
      letter-spacing: 0 !important;
      color: #fff;
      margin-top: 0;
    }
  }
  &__title {
    color: #fff;
    span {
      color: #E6B964;
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 150%;
    font-weight: 500;
    letter-spacing: -0.48px;
    color: #E6B964;
    margin-top: 14px;
  }
  &__icon {
    width: 55px;
    margin-bottom: 16px;
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    right: 30%;
    bottom: 0;
    width: 228px;
    img {
      width: 100%;
      display: block;
    }
  }
}
.questions {
  padding: 45px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 4px;
    background: #F1F7F7;
    border-radius: 20px;
    padding: 14px;
    margin-top: 25px;
  }
  &__item {
    background: #fff;
    border-radius: 12px;
    padding: 24px 24px 0 24px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding-bottom: 24px;
      cursor: pointer;
    }
    &-title {
      font-size: 18px;
      line-height: 155%;
      font-weight: 500;
    }
    &-btn {
      width: 21px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-hidden {
      display: none;
      padding-bottom: 24px;
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
    }
  }
  .item--active {
    background: rgba(168, 195, 195, 0.22);
  }
}
.connection {
  padding: 45px 0;
  &__inner {
    position: relative;
    background-image: url("../img/connection-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 24px;
    padding: 44px 530px 44px 54px;
  }
  &__img {
    position: absolute;
    right: 110px;
    bottom: 0;
    width: 420px;
    img {
      width: 100%;
      display: block;
    }
  }
  &__title {
    color: #fff;
    span {
      color: #E6B964;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 155%;
    font-weight: 500;
    color: #fff;
    margin-top: 8px;
  }
  &__form {
    position: relative;
    z-index: 2;
    max-width: 485px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 25px;
    &-check {
      margin-top: 16px;
    }
  }
}
.info2 {
  padding: 45px 0 90px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    p {
      font-size: 18px;
      line-height: 155%;
    }
    p:nth-child(2) ~ p {
      display: none;
    }
  }
  .hidden-content--active {
    p {
      display: block !important;
    }
  }
  .show-all {
    display: inline-block;
    cursor: pointer;
    margin-top: 24px;
    &:hover {
      span {
        text-decoration: unset;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      text-decoration: underline;
      color: #006B6B;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  .show-all--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: inline-block;
    }
  }
}

@media screen and (max-width: 1150px) {
  .main {
    &-slide__img {
      width: 400px;
    }
    &__title {
      font-size: 36px;
      letter-spacing: -0.72px;
    }
    &__subtitle {
      font-size: 26px;
      letter-spacing: -0.52px;
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    }
  }
  .connection {
    &__inner {
      padding-right: 400px;
    }
    &__img {
      right: 30px;
      width: 350px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &-slide {
      position: relative;
    }
    &-slide__desc {
      max-width: 400px;
    }
    &-slide__img {
      position: absolute;
      right: -26px;
      bottom: -26px;
      width: 300px;
      img {
        position: static;
        transform: translate(0, 0);
      }
    }
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &-img {
        padding-bottom: 37%;
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
    }
  }
  .products2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
  .product2-card {
    min-height: 230px;
    align-items: start;
    @media screen and (min-width: 577px) {
      &:nth-child(3) {
        grid-column: 1/3;
        .product2-card__img img {
          max-height: 100%;
        }
      }
    }
    &__img {
      position: absolute;
      top: 24px;
      left: 50%;
      transform: translate(-50%, 0);
      height: 100%;
      img {
        max-height: 112px;
      }
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 15px / 3));
    }
  }
  .banner {
    &__inner {
      align-items: center;
      padding: 60px 30px;
    }
    &__left {
      max-width: 450px;
    }
    &__right {
      .banner__title {
        font-size: 36px;
      }
    }
    &__title {
      font-size: 26px;
    }
    &__subtitle {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
    &__icon {
      margin-bottom: 10px;
    }
    &__img {
      display: none;
    }
  }
  .connection {
    &__inner {
      padding-right: 54px;
    }
    &__form {
      max-width: 100%;
    }
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 850px) {
  .products {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
}

@media screen and (max-width: 767px) {
  .main {
    &-slide__img {
      width: 250px;
    }
  }
  .info {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 25px;
    &-slide {
      padding: 34px 34px 88px 34px;
    }
    &-slide__desc {
      max-width: 242px;
    }
    &-slide__img {
      width: 200px;
      right: -20px;
      bottom: -20px;
    }
    &__title {
      font-size: 20px;
      letter-spacing: -0.4px;
    }
    &__subtitle {
      font-size: 14px;
      letter-spacing: -0.28px;
    }
    &__btn {
      height: 56px;
      padding: 5px 24px;
      margin-top: 24px;
      span {
        font-size: 14px;
      }
    }
    &__slider-nav {
      left: 34px;
      right: unset;
      bottom: 28px;
    }
    &__item {
      &-img {
        padding-bottom: 65%;
      }
      &-btn {
        span {
          font-size: 14px;
        }
      }
    }
  }
  .info {
    padding: 25px 0;
    &__item {
      padding: 34px 8px;
    }
  }
  .products2 {
    padding: 25px 0;
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .products {
    padding: 25px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
  }
  .banner {
    padding: 25px 0;
    &__inner {
      flex-direction: column;
      align-items: start;
      padding: 14px;
    }
    &__right {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__title {
      font-size: 20px !important;
    }
    &__subtitle {
      font-size: 14px;
      letter-spacing: -0.28px;
    }
    &__icon {
      width: 54px;
    }
  }
  .questions {
    padding: 25px 0;
  }
  .connection {
    padding: 25px 0;
    &__inner {
      border-radius: 14px;
      padding: 14px;
    }
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 14px;
    }
  }
  .info2 {
    padding: 25px 0 50px;
    &__text {
      gap: 15px;
      margin-top: 15px;
      p {
        font-size: 14px;
      }
    }
    .show-all {
      margin-top: 15px;
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .product-card {
    @media screen and (min-width: 360px) {
      &__img {
        padding-bottom: 170%;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .main {
    &-slide {
      padding: 34px 24px 88px 24px;
    }
  }
  .info {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .products {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}