.wrapper {
  overflow: hidden;
}
.h2 {
  font-size: 36px;
  line-height: 110%;
  font-weight: 600;
  letter-spacing: -0.72px;
}
.btn-brown {
  background: #F2DAAF;
  border-radius: 12px;
  color: #191919;
  &:hover {
    background: #E6B964;
    .btn-brown__arrow {
      background: #F2DAAF;
    }
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
  .btn-brown__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E6B964;
    border-radius: 88px;
    padding: 6px 10px;
    svg {
      width: 9px;
      height: auto;
    }
  }
}
.btn-brown2 {
  background: #E6B964;
  border-radius: 12px;
  &:hover {
    background: #F2DAAF;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.btn-gray {
  background: #F1F7F7;
  border-radius: 12px;
  &:hover {
    background: #268B8B;
    color: #fff;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.btn-green {
  background: #006B6B;
  border-radius: 12px;
  color: #fff;
  &:hover {
    background: #F1F7F7;
    color: #191919;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.socials {
  display: flex;
  align-items: center;
  gap: 10px;
}
.social {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F1F7F7;
  border-radius: 10px;
  padding: 5px;
  svg {
    max-width: 22px;
    width: auto;
    height: auto;
    path {
      transition: all 0.3s;
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  background: #1F8C8C;
  border-radius: 10px;
  opacity: 1 !important;
  &:after {
    display: none;
  }
  &:hover {
    background: #fff;
    svg path {
      stroke: #003737;
    }
  }
  svg {
    width: 9px;
    height: auto;
  }
  svg path {
    transition: all 0.3s;
  }
}
.form {
  &-group {
    input {
      width: 100%;
      height: 58px;
      background: #fff;
      border:  1px solid transparent;
      border-radius: 12px;
      padding: 0 24px;
      font-size: 14px;
      line-height: 188%;
      color: #191919;
      &::placeholder {
        color: #888888;
      }
    }
  }
  &-btn {
    width: 100%;
    height: 58px;
    padding: 5px 10px;
    span {
      font-size: 16px;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check-icon.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 22px;
      font-size: 10px;
      line-height: 100%;
      color: #A8C3C3;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 10.5px;
        height: 10.5px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1.4px solid #A8C3C3;
        border-radius: 50%;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #F2DAAF;
        }
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      width: calc(100% - 30px) !important;
    }
  }
  &__close {
    position: absolute;
    top: -42px;
    right: 0;
    width: 22px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__icon {
    width: 200px;
    margin: 0 auto 18px auto;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 30px;
    line-height: 110%;
    font-weight: 500;
    letter-spacing: -0.6px;
    color: #006B6B;
  }
  .center-title {
    text-align: center;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 155%;
    margin-top: 8px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 18px;
    &-check {
      margin-top: 8px;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 480px;
    background: #F1F7F7;
    border-radius: 24px;
    padding: 24px 32px;
  }
}


@media screen and (max-width: 576px) {
  .h2 {
    font-size: 26px;
    letter-spacing: -0.52px;
  }
  .form {
    &-group {
      input {
        height: 34px;
      }
    }
    &-btn {
      height: 34px;
      span {
        font-size: 14px;
      }
    }
  }
  .modal {
    &__icon {
      width: 140px;
      margin-bottom: 10px;
    }
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 14px;
    }
  }
}