.footer {
  background: #006B6B;
  &__location {
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    color: #fff;
  }
  &__schedule {
    font-size: 14px;
    line-height: 100%;
    color: #819E9E;
    margin-top: 10px;
  }
  &__social {
    background: #268B8B;
    &:hover {
      background: #f1f7f7;
      svg path {
        fill: #006B6B;
      }
    }
  }
  &__phones {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__phone {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #268B8B;
    border-radius: 12px;
    padding: 5px 25px;
    font-size: 18px;
    color: #fff;
    &:hover {
      background: #f1f7f7;
      color: #006B6B;
    }
  }
  &__btn {
    height: 40px;
    gap: 10px;
    padding: 5px 25px;
    i {
      width: 10px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 14px;
    }
  }
  &__group {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 168px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 140px;
    font-size: 12px;
    line-height: 130%;
    color: #7D9B9B;
  }
  &__menu {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    align-items: start;
    gap: 80px;
  }
  &__list {
    max-width: 160px;
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
    li a {
      font-size: 16px;
      line-height: 150%;
      font-weight: 500;
      color: #fff;
    }
    li a:hover {
      color: #7D9B9B;
    }
  }
  &__items {
    display: flex;
    align-items: start;
    gap: 10px;
  }
  &__item {
    max-width: 200px;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #D9EBEB;
    border-radius: 12px;
    padding: 5px 14px;
    &-text {
      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
    }
    &-icon {
      max-width: 32px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
  }
  &__text2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px 10px;
    p {
      font-size: 12px;
      line-height: 150%;
      color: #A8C3C3;
    }
    p a:hover {
      color: #F1F7F7;
    }
  }
}
.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  border-bottom: 1px solid #207A7A;
  padding: 24px 0;
  &__right {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
  }
}
.footer-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
  border-bottom: 1px solid #207A7A;
  padding: 24px 0;
  &__left {
    flex-grow: 1;
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__right {
    flex-shrink: 0;
  }
}
.footer-bottom {
  padding: 24px 0;
}

@media screen and (max-width: 1400px) {
  .footer {
    &__menu {
      gap: 24px;
    }
  }
  .footer-center {
    gap: 24px;
    &__left {
      flex-grow: unset;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__group {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer {
    &__phone {
      padding: 5px 15px;
    }
    &__btn {
      padding: 5px 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__socials {
      position: absolute;
      top: 24px;
      right: 0;
      gap: 5px;
    }
    &__phones {
      flex-grow: 1;
    }
    &__phone {
      width: 100%;
      font-size: 16px;
    }
    &__btn {
      flex-grow: 1;
      padding: 5px 25px;
    }
    &__menu {
      flex-direction: column;
      gap: 6px;
    }
    &__items {
      flex-direction: column;
    }
  }
  .footer-top {
    position: relative;
    flex-direction: column;
    align-items: start;
    gap: 20px;
    &__left {
      width: 100%;
      padding-right: 95px;
    }
    &__right {
      width: 100%;
    }
  }
  .footer-center {
    align-items: start;
    gap: 20px;
    &__left {
      align-items: start;
      gap: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__location {
      font-size: 14px;
    }
    &__socials {
      position: static;
      flex-direction: column;
    }
    &__phones {
      max-width: 185px;
      width: 100%;
      flex-grow: unset;
      flex-direction: column;
    }
    &__phone {
      span {
        font-size: 14px;
      }
    }
    &__btn {
      position: absolute;
      left: 0;
      bottom: 16px;
      width: 100%;
      span {
        font-size: 14px;
      }
    }
    &__group {
      order: 2;
      align-items: center;
      text-align: center;
    }
    &__menu {
      align-items: center;
      text-align: center;
      gap: 4px;
    }
    &__list {
      gap: 4px;
      li a {
        font-size: 14px;
      }
    }
    &__text2 {
      flex-direction: column;
      text-align: center;
    ;
    }
  }
  .footer-top {
    padding-top: 16px;
    padding-bottom: 66px;
    &__left {
      padding-right: 0;
    }
    &__right {
      justify-content: space-between;
    }
  }
  .footer-center {
    flex-direction: column;
    align-items: center;
    padding: 16px 0;
    &__left {
      gap: 10px;
    }
  }
  .footer-bottom {
    padding: 16px 0;
  }
}