.header {
  position: relative;
  z-index: 5;
  padding: 25px 0;
  &__location {
    font-size: 14px;
    line-height: 100%;
    font-weight: 500;
  }
  &__schedule {
    font-size: 14px;
    line-height: 100%;
    color: #819E9E;
    margin-top: 10px;
  }
  &__social {
    &:hover {
      background: #268B8B;
      svg path {
        fill: #fff;
      }
    }
  }
  &__phones {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__phone {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F1F7F7;
    border-radius: 12px;
    padding: 5px 25px;
    font-size: 18px;
    color: #006B6B;
  }
  &__btn {
    height: 40px;
    gap: 10px;
    padding: 5px 25px;
    i {
      width: 10px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 14px;
    }
  }
  &__logo {
    width: 168px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 140px;
    font-size: 12px;
    line-height: 130%;
    color: #888888;
  }
  &__yandex {
    height: 50px;
    gap: 10px;
    padding: 5px 25px;
    &:hover {
      .btn-rating {
        background: #F2DAAF;
      }
    }
    span {
      font-size: 14px;
    }
    .btn-rating {
      background: #E6B964;
      border-radius: 88px;
      padding: 4px 8px;
    }
  }
  &__favorite {
    position: relative;
    width: 62px;
    height: 50px;
    &:hover {
      .header__favorite-icon {
        svg path {
          stroke: #fff;
        }
      }
    }
    &-icon {
      width: 22px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &-number {
      position: absolute;
      top: -6px;
      right: 5px;
      background: #E6B964;
      border-radius: 72px;
      padding: 2px 6px;
      font-size: 10px;
      color: #191919;
    }
  }
  &__basket {
    height: 50px;
    gap: 10px;
    padding: 5px 25px;
    &:hover {
      .btn-icon svg path {
        fill: #191919;
      }
    }
    span {
      font-size: 14px;
    }
    .btn-icon {
      width: 18px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .btn-number {
      background: #E6B964;
      border-radius: 88px;
      padding: 4px 8px;
      color: #191919;
    }
  }
  .swiper {
    overflow: unset;
  }
  .menu-item {
    position: relative;
    height: 60px;
    padding: 5px 20px;
    transition: all 0.3s;
    span {
      font-size: 16px;
    }
  }
  .menu-item__show {
    justify-content: start;
    gap: 14px;
    padding-right: 39px;
    cursor: pointer;
    &:hover {
      .catalog-icon svg path {
        fill: #fff;
      }
      .catalog-arrow svg path {
        fill: #fff;
      }
    }
    .catalog-icon {
      flex-shrink: 0;
      width: 18px;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .catalog-arrow {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      width: 15px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
  .menu-item__show--active {
    background: #268B8B;
    color: #fff;
    .catalog-icon svg path {
      fill: #fff;
    }
    .catalog-arrow svg path {
      fill: #fff;
    }
  }
  .menu-item__hidden {
    position: absolute;
    z-index: 5;
    top: 100%;
    left: 0;
    width: 312px;
    background: #fff;
    border-radius: 14px;
    padding: 14px 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
  }
  .menu-item__hidden--active {
    opacity: 1;
    pointer-events: auto;
  }
  .menu-item__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 4px;
  }
  .menu-subitem {
    position: relative;
  }
  .menu-subitem:hover,
  .menu-subitem--active {
    .menu-subitem__hidden {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .menu-subitem__show {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    padding: 5px 18px;
    color: #006B6B;
    &:hover {
      color: #fff;
      .menu-subitem__arrow {
        svg path {
          fill: #fff;
        }
      }
    }
  }
  .menu-subitem__text {
    font-size: 14px;
  }
  .menu-subitem__arrow {
    width: 10px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  .menu-subitem__hidden {
    position: absolute;
    top: 0;
    left: calc(100% + 4px);
    width: 312px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -4px;
      width: 4px;
      height: 100%;
      opacity: 0;
    }
  }
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  &__right {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
  }
}
.header-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-top: 18px;
  &__left {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
  }
}
.header-bottom {
  margin-top: 28px;
}
.search {
  position: relative;
  z-index: 5;
  width: 100%;
  &--active {
    .search__result {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__wrap {
    flex-grow: 1;
  }
  &__close {
    display: none;
  }
  &__show {
    position: relative;
    width: 100%;
    input {
      width: 100%;
      height: 50px;
      border: 1px solid #CCDFDF;
      border-radius: 10px;
      padding: 0 24px 0 56px;
      font-size: 16px;
      line-height: 150%;
      color: #191919;
      &::placeholder {
        color: #A8C3C3;
      }
    }
  }
  &__icon {
    position: absolute;
    top: 50%;
    left: 24px;
    transform: translate(0, -50%);
    width: 15px;
    img {
      width: 100%;
    }
  }
  &__result {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    max-height: 256px;
    background: #fff;
    border-radius: 14px;
    padding: 8px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
    overflow-y: auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 4px;
  }
  &-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 14px;
    background: #F1F7F7;
    border-radius: 8px;
    padding: 10px;
    &__img {
      width: 54px;
      flex-shrink: 0;
      img {
        width: 100%;
        border-radius: 14px;
      }
    }
    &__desc {
      flex-grow: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 14px;
    }
    &__group {
      max-width: 240px;
    }
    &__name {
      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
      color: #006B6B;
    }
    &__article {
      font-size: 12px;
      line-height: 150%;
      color: #4B6B6B;
      margin-top: 2px;
    }
    &__price {
      flex-shrink: 0;
      font-size: 16px;
      line-height: 100%;
      font-weight: 600;
      letter-spacing: -0.32px;
    }
  }
  &__mob-btn {
    display: none;
  }
}

@media screen and (max-width: 1250px) {
  .header-center {
    @media screen and (min-width: 993px) {
      &__left {
        flex-direction: column;
        align-items: start;
        gap: 10px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__phone {
      padding: 5px 15px;
    }
    &__btn {
      padding: 5px 16px;
    }
    &__yandex {
      padding: 5px 16px;
    }
    &__favorite {
      width: 50px;
    }
    &__basket {
      padding: 5px 16px;
    }
  }
  .header-center {
    gap: 10px;
  }
  .search {
    &-card {
      &__img {
        width: 48px;
      }
      &__name {
        font-size: 13px;
      }
      &__price {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 16px 0 32px;
    &__socials {
      gap: 5px;
    }
    &__phones {
      flex-grow: 1;
    }
    &__phone {
      width: 100%;
      font-size: 16px;
    }
    &__btn {
      flex-grow: 1;
      padding: 5px 25px;
    }
    &__text {
      display: none;
    }
    &__yandex {
      display: none;
    }
    &__favorite {
      width: 62px;
    }
    &__basket {
      padding: 5px 25px;
      .btn-text {
        display: none;
      }
    }
    .menu-item {
      height: 32px;
      border-radius: 6px;
      padding: 4px 8px;
      span {
        font-size: 14px;
      }
    }
    .menu-item__show {
      gap: 4px;
      padding: 4px 31px 4px 16px;
      .catalog-icon {
        width: 14px;
      }
      .catalog-arrow {
        right: 12px;
      }
    }
  }
  .header-top {
    flex-direction: column;
    align-items: start;
    &__right {
      width: 100%;
    }
  }
  .header-center {
    gap: 20px;
    margin-top: 24px;
  }
  .header-bottom {
    margin-top: 14px;
  }
  .search {
    &__show {
      input {
        padding: 0 10px 0 42px;
      }
    }
    &__icon {
      left: 10px;
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    &__phones {
      display: none;
    }
    &__btn {
      flex-grow: unset;
    }
    &__favorite {
      width: 40px;
      height: 40px;
      &-icon {
        width: 15px;
      }
      &-number {
        right: 0;
      }
    }
    &__basket {
      height: 40px;
      .btn-icon,
      .btn-number {
        display: none;
      }
      .btn-text {
        display: block;
      }
    }
    .swiper-slide {
      width: auto !important;
      flex-grow: 1;
    }
    .menu-item {
      padding: 4px 20px;
    }
    .menu-item__show {
      padding: 4px 39px 4px 20px;
      .catalog-arrow {
        right: 20px;
      }
    }
    .menu-subitem--active {
      .menu-subitem__hidden {
        height: auto;
        overflow: unset;
      }
    }
    .menu-subitem__hidden {
      position: static;
      height: 0;
      padding-left: 20px;
      opacity: 1;
      pointer-events: auto;
      overflow: hidden;
    }
  }
  .header-top {
    &__left {
      display: none;
    }
    &__right {
      justify-content: space-between;
    }
  }
  .header-center {
    gap: 15px;
    margin-top: 20px;
  }
  .search {
    &__wrap {
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      padding: 100px 25px 25px 25px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__close {
      position: absolute;
      top: 30px;
      right: 25px;
      width: 32px;
      width: 40px;
      display: block;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
    &-card {
      &__img {
        width: 54px;
      }
      &__name {
        font-size: 14px;
      }
      &__price {
        font-size: 16px;
      }
    }
    &__mob-btn {
      width: 40px;
      height: 40px;
      background: transparent;
      border: 1px solid #CCDFDF;
      border-radius: 10px;
      display: flex;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding-top: 12px;
  }
  .header-top {
    &__left {
      display: none;
    }
    &__right {
      width: 100%;
      justify-content: space-between;
    }
  }
}

@media screen and (max-width: 450px) {
  .header {
    &__logo {
      width: 160px;
    }
    &__favorite {
      display: none;
    }
    &__basket {
      padding: 5px 15px;
    }
    .menu-item__hidden {
      width: 290px;
    }
    .menu-subitem__hidden {
      width: 290px;
    }
  }
  .header-center {
    &__right {
      gap: 5px;
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__logo {
      width: 140px;
    }
  }
  .search {
    &__wrap {
      padding: 75px 15px 25px 15px;
    }
  }
}